.Footer{
	  position: relative;
	  right: 0;
	  bottom: 0;
	  left: 0;
	  padding: 1rem;
	  {/*background-color: #efefef;*/}
	  text-align: center;
}

.rows{
	display: flex;
	display: -ms-flexbox;
	-ms-flex-wrap: wrap;
	flex-wrap: wrap;
	margin-left: -15px;
	margin-right: -15px;
}

.container-fluid{

	width: 100%;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	background: #BA8B02;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to bottom, #030405, #151f26);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to bottom, #030405, #151f26); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */


}

.container{
	max-width: 1200px;
}