.project_back,.team-back,.about_back,.our-story-card-background{
	z-index: 0;
    position: absolute;
    
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}
.project_back,.team-back,.contact_back,.about_back{
	position: relative;
	width: 100vw;
}
.image-wrapper{
	overflow: hidden;
}

img{
	max-width: 100%;
	height: 100vh;
	width: 100vw;
	border: 0;
}
bg-img{
	height: 100vh;
}
.App{
	background-color: black;
}
.image-wrapper-gradient-about,.image-wrapper-gradient,.image-wrapper-gradient-team{  
	
  background: rgba(0, 0, 0, 0.4); 
  background-image:linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.8) 100%);
  height: 100vh;
  margin: 0 -9999rem;
  padding: 0.25rem 9999rem;
  position: relative;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display:inline-block;
}

.image-wrapper-gradient-about{
	background:rgba(0,0,0,0.6);
	background-image:linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.8) 100%);
}
.card-text,.card-text-description,.card-text-contact,.about,.card-text-about,.card-text_home,.card-text-team,.card-text-team-photo{
	position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);	
	
    width: 100vw;
    padding: 75px 0;
    max-width: 640px;
    margin: 0 auto;
    text-align: center;
    z-index: 1;
}

.card-text-team-photo{
	height: 50%;
	width: 50%;
}
.about{
	top:65%;
}
.card-text-contact{
	max-width: 1000px;
}
.card-text-about{
	top: 20%;
	left: 20%;
	padding: 0;
}

.card-text-description{
	max-width: 1000px;
	top:78%;
}
.card-text{
	max-width: 1000px;

}
.card-text-team{
	left:28%;
}
.card-text_home{
	font-size: 1.625rem;
    font-weight: normal;
}

.our-story-card{
	position: relative;
    
    {/*padding: 50px 5% 0px 5%;*/}
    margin-bottom: 0;
    background: transparent;
    color: white;
}

bg-img hero-card{
	width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    -webkit-transform: scale(1.25) translateY(-10%);
    -moz-transform: scale(1.25) translateY(-10%);
    -ms-transform: scale(1.25) translateY(-10%);
    -o-transform: scale(1.25) translateY(-10%);
    transform: scale(1.25) translateY(-10%);

}	

.v1 {
  border-left: 8px solid gold;
  height: 55px;
  position: absolute;
  left: 68%;
  margin-left: -3px;
  top: 35px;
}

.v2{
	  border-left: 8px solid gold;
	  height: 310px;
	  position: absolute;
	  right: 20%;
	  margin-left: -3px;
	  display: inline-block;
	  top: 1px;
}

.application{
	float: right;
	left: 50%;
	border-style: solid ;
	box-sizing: border-box;
	border-color: white;
}

div{
	display: block;
}

@media only screen and (max-width: 949px) and (min-width: 550px), only screen and (max-width: 1449px) and (min-width: 950px), only screen and (min-width: 1450px)
.our-story-card {
    padding: 70px 45px;
}


.our-story-card-img-container{
	width: 48%;
	height:100%;
	display: flex;
	-webkit-box-flex:0;
	flex: 0 1 auto;
	box-sizing: border-box;
}
.animation-card.watchOnTv .our-story-card-animation-container ,.{
    margin: -10% -5% -5% 
 }

 .animation-card .our-story-card-animation-container {
    position: relative;
    overflow: hidden;
}

.animation-card.watchOnTv .our-story-card-img, .animation-card.watchOnDevice .our-story-card-img {
    position: relative;
    z-index: 2;
}

.our-story-card-img{
	cursor: default;
}
.animation-card.watchOnTv .our-story-card-animation ,.our-story-card-animation-upper {
    width: 100%;
    height: 100%;
    max-width: 73%;
    max-height: 54%;
    position: absolute;
    top: 44%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}

.our-story-card-animation-upper
{
	width: 41%;
	height: 50%;
}

.animation-card.watchOnTv .our-story-card-video, .animation-card.watchOnDevice .our-story-card-video {
    width: 100%;
    height: 100%;
}

.animation-card,.animation-card-container{
	max-width: 1100px;
    margin: 0 auto;
    display: -webkit-box;
    display: -webkit-flex;
    display: -moz-box;
    display: -ms-flexbox;
    display: fle;
    -webkit-box-align: center;
    -webkit-align-items: center;
    -moz-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -webkit-justify-content: space-between;
    -moz-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}

.animation-card .our-story-card-text {
    width: 52%;
    height: 100%;
    -webkit-box-flex: 0;
    -webkit-flex: 0 1 auto;
    -moz-box-flex: 0;
    -ms-flex: 0 1 auto;
    flex: 0 1 auto;
    padding: 3rem 3rem 0 0;
    z-index: 3;
}

@media only screen and (max-width: 949px) and (min-width: 550px), only screen and (max-width: 549px) and (min-width: 400px), only screen and (max-width: 399px) and (min-width: 350px), only screen and (max-width: 349px)
.animation-card .animation-card-container {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -moz-box-orient: vertical;
    -moz-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}
@media only screen and (max-width: 949px) and (min-width: 550px), only screen and (max-width: 549px) and (min-width: 400px), only screen and (max-width: 399px) and (min-width: 350px), only screen and (max-width: 349px)
.animation-card.watchOnTv .our-story-card-text {
    margin: 0;
}

@media only screen and (max-width: 949px) and (min-width: 550px), only screen and (max-width: 549px) and (min-width: 400px), only screen and (max-width: 399px) and (min-width: 350px), only screen and (max-width: 349px)
.animation-card .our-story-card-text {
    width: 100%;
    text-align: center;
    padding: 0;
}