.scrolling-wrapper {
  border-width: 75px 500px;
  height: 350px;
  
  margin-bottom: 50px;
  width: auto;
  border-color: #3b0504;
  background-color:white;{/*#3b0504;*/}
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  
 }
 

 .scrolling-wrapper::-webkit-scrollbar {
  display: none;
}
  

code {
  font-family:  'Courier New',source-code-pro, Menlo, Monaco, Consolas,monospace;
}
.button{
	cursor: pointer;
}

.content-wrapper{
	background-color: #3b0504;
}
.image-card{
	width:250px;
	height:250px;
}

.owl-item{
	width: 250px	!important;

}

.SelectorBox{
  width:290px !important;
  
  opacity: 1;
  padding: 3px;
  height: 270px !important;
 
  {/*margin-left: -20px;
  margin-right: -20px;*/}
  position: relative;
  box-shadow: rgb(51, 51, 51) 10px 10px 15px;
  cursor: pointer;
  border-style: solid;
  border-color: rgb(255,255,255);
  border-width: 15px 15px 15px;
}

.indicator{
  transform: translate(-50%,-50%);
  position: absolute;
}

#Browse,.SelectorTag{
  
  width:290px;
  position: element(SelectorBox);
  padding-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  
  
  text-align: center;
  padding-top: 10px;
  box-sizing: border-box;
  border-style: solid;
  border-color: rgb(255,255,255);
  border-image: initial;
  

}

#Browse{
  width: fit-content;
  border-style: hidden;
}


img{
  vertical-align: baseline;
  cursor: pointer;
}

